@import "../../variables.scss";

.notes-wrapper{
    background: #eee;
    margin: 10px 0;
    padding: 15px 0;

    .notes{
        .note{

            .title{
                margin: 0;
                padding: 5px;
                color: #333;
            }

            .description{
                font-weight: normal;
                padding: 5px 10px;
                margin: 0;
                height: 50px;
                overflow: hidden;
                color: #565656;
                font-style: italic;
            }

            .actions{
                padding: 10px 5px;

                a{
                    text-decoration: none;
                    background: #ececec;
                    border: 1px solid #888;
                    border-radius: 3px;
                    color: #333;
                    padding: 3px 5px;

                    &:hover{
                        background: #aaa;
                    }
                }
            }
        }
    }

    .actions{
        margin: 15px 0 0;
    }
}
